<template>
    <div class="cs-right-page" v-bind:style="mianStyle">
        
        <div class="cs-right-hd">
            <!-- 表头 -->
            <span class="el-dialog__title">实名认证
                <span v-if="selectRowData.deptInfo && selectRowData.deptInfo.type === '公安'">(公安)</span>
                <span v-else-if="selectRowData.deptInfo && selectRowData.deptInfo.type === '小区'">(居民)</span>
                <span v-else>(其他)</span>
            </span>

        </div>
        <div class="cs-right-body">
            <!-- 表内容 -->
            <div class="cs-box2 cs-form-item cs-body-item cs-box-aaa">
                <div class="cs-box2 cs-box-flex1 cs-box-aaa">
                    <div class="labeltext">姓名：</div>
                    <div class="cs-box-flex-auto cs-form-item-content cs-form-sm-content cs-box-div">{{selectRowData.realname}}</div>
                </div>

                <div class="cs-box2  cs-box-aaa cs-box-flex3">
                    <div class="labeltext">联系电话：</div>
                    <div class="cs-box-flex-auto cs-form-item-content cs-box-div">{{selectRowData.cellphone}}</div>
                </div>
            </div>

            <div class="cs-box2 cs-box-aaa cs-form-item  cs-body-item" v-if="selectRowData.deptInfo && selectRowData.deptInfo.type != '小区'">
                <div class="cs-box2 cs-box-aaa cs-box-flex1">
                    <div class="labeltext">职务：</div>
                    <div class="cs-box-flex-auto cs-box-div cs-form-item-content cs-form-sm-content">{{selectRowData.jobsName}}</div>
                </div>

                <div class="cs-box2 cs-box-aaa cs-box-flex3">
                    <div class="labeltext">工作单位：</div>
                    <div class="cs-box-flex-auto cs-box-div cs-form-item-content">{{selectRowData.deptInfo.name}}</div>
                </div>
            </div>


            <div class="cs-box2 cs-form-item cs-box-aaa cs-body-item">
                <div class="cs-box2 cs-box-aaa cs-box-flex1">
                    <div  class="labeltext">地区：</div>
                    <div class="cs-box-flex-auto cs-box-div cs-form-item-content cs-form-sm-content">{{selectRowData.deptInfo.name}}</div>
                </div>

                <div class="cs-box2 cs-box-aaa cs-box-flex3">
                    <div  class="labeltext">单位类型：</div>
                    <div class="cs-box-flex-auto cs-box-div cs-form-item-content">{{selectRowData.deptInfo.type}}</div>
                </div>
            </div>

            <div class="cs-box2 cs-box-aaa cs-form-item  cs-body-item">
                <div class="cs-box2 cs-box-aaa cs-box-flex1"  v-if="selectRowData.deptInfo && selectRowData.deptInfo.type === '公安'">
                    <div  class="labeltext">警号：</div>
                    <div class="cs-box-flex-auto cs-box-div cs-form-item-content cs-form-sm-content">{{selectRowData.policemanCode}}</div>
                </div>

                <div class="cs-box2 cs-box-aaa  cs-box-flex1" v-if="selectRowData.deptInfo && selectRowData.deptInfo.type === '小区'">
                    <div  class="labeltext">小区：</div>
                    <div class="cs-box-flex-auto cs-form-item-content cs-form-sm-content">{{selectRowData.areaInfo != null ? selectRowData.areaInfo.name : '' }}</div>
                </div>

                <div class="cs-box2 cs-box-aaa cs-box-flex3">
                    <div  class="labeltext">申请时间：</div>
                    <div class="cs-box-flex-auto cs-box-div cs-form-item-content">{{selectRowData.authingTime|moment}}</div>
                </div>
            </div>


            <div class="cs-form-item cs-form-sfz">
                <div class="cs-label-title">证件照片：</div>
                <div class="cs-box">
                    <div class="cs-item-left cs-item">
                        <div class="cs-sfz">
                            <span v-if="selectRowData.deptInfo && selectRowData.deptInfo.type === '公安'">证件照正面</span>
                            <span v-else>身份证正面照</span>
                        </div>
                        <img @click="open($event)" :src="selectRowData.idcardImage1" alt="" />
                    </div>
                    <div class="cs-item-right cs-item">
                        <div class="cs-sfz">
                            <span v-if="selectRowData.deptInfo && selectRowData.deptInfo.type === '公安'">证件照反面</span>
                            <span v-else>身份证反面照</span>
                        </div>
                        <img @click="open($event)" :src="selectRowData.idcardImage2" alt="" />
                    </div>
                </div>
            </div>

            <div class="cs-form-item "  v-if="selectRowData.auth === 'authed'">
                <div class="cs-label-title cs-spyi">审批意见<b>[通过]</b>：</div>
                <div class="cs-smrz-xq-authmsg">{{selectRowData.authmsg}}</div>
            </div>


            <div class="cs-form-item "  v-if="selectRowData.auth === 'authFail'">
                <div class="cs-label-title cs-spyi">审批意见<b>[驳回]</b>：</div>
                <div class="cs-smrz-xq-authmsg">{{selectRowData.authmsg}}</div>
            </div>




            <div class="cs-form-item" v-if="selectRowData.auth === 'authing'">
                <div class="cs-label-title">审批意见：</div>
                <el-input
                    type="textarea"
                    :autosize="{ minRows: 2, maxRows: 10}"
                    placeholder="在这里输入审批意见"
                    v-model="authMsg">
                </el-input>
            </div>



            <div class="cs-box2 cs-form-item cs-body-item cs-box-aaa"  v-if="selectRowData.auth == 'authed' || selectRowData.auth == 'authFail'">
                <div class="cs-box2 cs-box-flex1 cs-box-aaa">
                    <div class="labeltext">审批人：</div>
                    <div class="cs-box-flex-auto cs-form-item-content cs-form-sm-content cs-box-div">{{selectRowData.authInfo ?selectRowData.authInfo.realname : ''}}</div>
                </div>

                <div class="cs-box2  cs-box-aaa cs-box-flex3">
                    <div class="labeltext">审批时间：</div>
                    <div class="cs-box-flex-auto cs-form-item-content cs-box-div">{{selectRowData.auditedTime|moment}}</div>
                </div>
            </div>

           

        </div>

        <div class="cs-right-footer cs-sfz-footer" v-if="selectRowData.auth == 'authing'">
            <el-button :plain="true" type="primary" v-on:click="updateAuth(1)">通过</el-button>
            <el-button :plain="true" type="primary" v-on:click="updateAuth(2)">驳回</el-button>
        </div>
    </div>


</template>

<script>
export default{
    props: {
        mapHeight:{
            type:Number,
            default:600
        },
        isShow_rightbox:{
            type:Boolean,
            default:false
        },
        selectRowData:{}

    },
    data:function () {
        return {
            authMsg:"",
            mianStyle:""
        }
    },
    methods:{
        resetData(flag) {

        },
        open(e){

            const Bus = global.eventHub;
            if(e.target.src){
                 Bus.$emit("detailimg",e.target.src)
            }
           
        },
        hideUpdateModel() {


        },
        updateAuth(type) {
            //审核认证
            let that = this;
            var _param = {
                token:global.data.getToken(),
                id:that.selectRowData.id,
            };
            if (type == 1) {
                _param.auth ="authed";
                _param.authMsg = !!that.authMsg ? that.authMsg : "审核通过";
            } else {
                _param.auth ="authFail";
                _param.authMsg = !!that.authMsg ? that.authMsg : "审核驳回";
            }
            that.$http.post(global.config.weburl+'api/user/updateAuthStatus.do',_param).then(function(res) {
                if (res.data.code==0) {
                    this.$message({
                        type: 'success',
                        message: "审核成功"
                    });
                    that.$emit('reloadList');
                } else {
                    global.tools.handleError(res.data);
                }
            },function(error){
                this.$message({
                    type: 'error',
                    message: "审核失败"
                });
            });
        }
    },
    created: function(){
        this.mianStyle = {
            height:this.mapHeight +"px"
        }
        this.authMsg = "";



        console.log(this.selectRowData);
        //this.resetData(this.selectRowData);
    }
}
</script>
<style scoped>
.cs-right-body{background: #eef1f6;padding:4%;width: 100%;box-sizing: border-box;}
.cs-body-item{width: 100%;}
.cs-body-item .cs-box-aaa{width: 50%; margin:3px 0;} 
.cs-form-item .labeltext{white-space: nowrap; vertical-align: baseline;height: 20px; padding: 0;margin: 0;font-size: 14px; color: #323335; width: 80px;text-align: left;}
.cs-box-div{width: 65%;}
.cs-form-item .cs-form-item-content {word-break: break-all;font-size: 14px;}
.cs-form-item .cs-form-sm-content{}
.cs-right-footer{padding: 20px 0;}
.cs-form-item{width: 100%; }
.cs-form-sfz{min-height: 80%; font-size: 14px; color: #323335; margin: 20px 0;}

.cs-item{width: 48%;height: 100%;padding: 1px; color: #a9aaae;}
.cs-item img{width: 100%;height: 150px; background: #fff; padding: 5px;}
.cs-sfz{width: 100%;height: 15%;text-align: center; display: block; background: #eef1f6;}
.cs-right-hd{width: 100%;padding: 2% 0;background: #d5e3ec; text-align: center; /*justify-content:center;align-items:center;*/}
.cs-sfz-footer{width: 100%;padding:10px 0; text-align: center;color: #323335; background: #eef1f6;font-size: 14px;}
.cs-sfz-footer button{background: #6b91a6; color: #fff; font-size: 14px;}
/*.cs-page-content{margin: 0;}*/
.cs-sptime{margin-right: 40%;}
.cs-smrz-xq-authmsg{text-indent: 30px;padding-bottom: 5px;}
</style>


